.profile-picture {
    height: 40px;
    width: 40px;
    border-collapse: separate;
}

.User {
    display: block;
    height: 69px;
    width: auto;
    background-color: white;
    margin: 5px;
    color: black;
    border: 2px solid #EAEDEE;
    border-radius: 12px;
    box-sizing: border-box;
}

.content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 16px;
    margin-top: 14px;
    margin-bottom: 14px;
}

.name-text {
    color: black;
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 18px;
}

.text-content {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 12px;
    color: #96999C;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: -0.45769229531288147px;
    text-align: left;
}